.widget-coupon
{
  display: -webkit-box;
  background: #ff849b;
  min-height: 75px;
  border-radius: 2px;
  margin-bottom: 15px;
  color: #fff;

  .left-border{
    display: -webkit-box;
    padding: 4px 0px 0px 0px;
    .inside{
      position: relative;
      width: 4px;
      right: 2px;
      background: url(data:image/png;base64,) repeat-y;
    }
  }
  .price{
    display: -webkit-box;
    -webkit-box-orient:vertical;
    -webkit-box-pack:center;
    width: 70px;
    .type{
      margin-right: 2px;
      text-align: center;
      //color: #fff;
      font-size: 10px;
      margin-bottom: 5px;
    }
    .contain{
      margin-right: 4px;
      display: -webkit-box;
      -webkit-box-pack: center;
      .contain-left{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-box-pack: end;
        //color: #fff;
        font-size: 12px;
        margin-bottom: 7px;
      }
      .contain-right{
        //color: #fff;
        line-height: 40px;
        font-weight: 200;
        font-size: 40px;
        font-family: Arial;
      }
    }
  }
  .split{
    display: -webkit-box;
    -webkit-box-orient:vertical;
    -webkit-box-pack:justify;
    width: 13px;
    .top{
      position: relative;
      width: 13px;
      height: 13px;
      bottom: 6px;
      background: #fff;
      border-radius: 7px;
    }
    .mid{
      display: -webkit-box;
      -webkit-box-pack:center;
      -webkit-box-flex: 1;
      .mid-in{
        border-right: 1px dashed #fff;
      }
    }
    .bottom{
      position: relative;
      width: 13px;
      height: 13px;
      top: 6px;
      background: #fff;
      border-radius: 7px;
    }
  }
  .details{
    display: -webkit-box;
    -webkit-box-orient:vertical;
    -webkit-box-pack:center;
    -webkit-box-flex: 1;
    padding: 10px 0px 10px 12px;
    .notice{
      font-size: 14px;
      //color: #fff;
    }
    .date{
      font-size: 10px;
      //color: #fff;
    }
  }
  .choosen_btn{
    display: -webkit-box;
    -webkit-box-orient:vertical;
    -webkit-box-pack:center;
    width: 30px;
    padding-right: 5px;
    .tap{
      display: -webkit-box;
      -webkit-box-pack: center;
      -webkit-box-orient: vertical;
      text-align: -webkit-center;
      width: 21px;
      height: 21px;
      background: #fff;
      border-radius: 12px;
      .tap-in{
        display: -webkit-box;
        -webkit-box-pack: center;
        -webkit-box-orient: vertical;
        text-align: center;
        width: 19px;
        height: 19px;
        border-radius: 10px;
        background: #fff;

      }
      .icon{
        display: none;
        position: relative;
      }
      &.cur{
        .tap-in{
          background: #fff;
        }
        .icon{
          display: inline-block;
        }
      }
    }
  }
  .right-border{
    display: -webkit-box;
    padding: 4px 0px 0px 0px;
    .inside{
      position: relative;
      width: 4px;
      left: 2px;
      background: url(data:image/png;base64,) repeat-y;
    }
  }
  &.unavailable{
    background: #fff;
    color: #999;
    border: 1px solid #cccccc;
    .choosen_btn{
      display: none;
    }
    .top{
      border-bottom: 1px solid #cccccc;
      bottom: 9px;
    }
    .mid{
      .mid-in{
        border-right: 1px dashed #cccccc;
      }
    }
    .bottom{
      border-top: 1px solid #cccccc;
      top:9px;
    }
    .left-border{
      display: -webkit-box;
      padding: 4px 0px 0px 0px;
      .inside{
        position: relative;
        width: 4px;
        right: 3px;
        background: url(data:image/png;base64,) repeat-y;
      }
    }
    .right-border{
      display: -webkit-box;
      padding: 4px 0px 0px 0px;
      .inside{
        position: relative;
        width: 4px;
        left: 3px;
        background: url(data:image/png;base64,) repeat-y;
      }
    }
  }
  &.no_choose_btn{
    .choosen_btn{
      display: none;
    }
  }
}
